.embla{display:flex !important;flex-direction:row-reverse;align-items:flex-end;max-width:100%;margin:auto;--slide-height: 19rem;--slide-spacing: 1rem;--slide-size: 100%;--slide-spacing-sm: 1.6rem;--slide-size-sm: 50%;--slide-spacing-lg: 2rem;--slide-size-lg: calc(100% / 4)}.embla__viewport{overflow:hidden}.embla__container{backface-visibility:hidden;display:flex;touch-action:pan-y pinch-zoom;margin-left:0 !important;height:auto;gap:1rem;padding-left:1rem;padding-right:1rem;box-sizing:content-box}@media(min-width: 750px){.embla__container{margin-left:0 !important;height:auto;gap:1rem}}@media(min-width: 1200px){.embla__container{margin-left:calc(var(--slide-spacing-lg)*-1)}}.embla__slide{min-width:0;padding-left:0 !important;flex:0 0 auto;max-width:300px}@media(min-width: 750px){.embla__slide{padding-left:0 !important;flex:0 0 auto}}@media(min-width: 1200px){.embla__slide{flex:0 0 var(--slide-size-lg);padding-left:var(--slide-spacing-lg)}}.embla__slide__number{box-shadow:inset 0 0 0 .2rem var(--detail-medium-contrast);font-size:4rem;font-weight:600;display:flex;align-items:center;justify-content:center;height:auto;user-select:none}.embla__controls{display:grid;grid-template-columns:auto 1fr;justify-content:space-between;gap:1.2rem;margin-top:1.8rem}.embla__buttons{display:grid;grid-template-columns:repeat(2, 1fr);gap:.6rem;align-items:center;justify-content:space-between}.embla__button{-webkit-tap-highlight-color:rgba(var(--text-high-contrast-rgb-value), 0.5);-webkit-appearance:none;appearance:none;background-color:rgba(0,0,0,0);touch-action:manipulation;display:inline-flex;text-decoration:none;cursor:pointer;border:0;padding:0;margin:0;box-shadow:inset 0 0 0 .2rem var(--detail-medium-contrast);width:3.6rem;z-index:1;border-radius:50%;color:var(--text-body);display:flex;align-items:center;justify-content:center}.embla__button:disabled{color:var(--detail-high-contrast)}.embla__button__svg{width:35%;height:35%}.embla__dots{display:flex;flex-wrap:wrap;justify-content:flex-end;align-items:center;margin-right:-.6rem}.embla__dot{-webkit-tap-highlight-color:rgba(var(--text-high-contrast-rgb-value), 0.5);-webkit-appearance:none;appearance:none;background-color:rgba(0,0,0,0);touch-action:manipulation;display:inline-flex;text-decoration:none;cursor:pointer;border:0;padding:0;margin:0;width:2.6rem;height:2.6rem;display:flex;align-items:center;justify-content:center;border-radius:50%}.embla__dot:after{box-shadow:inset 0 0 0 .2rem var(--detail-medium-contrast);width:1.4rem;height:1.4rem;border-radius:50%;display:flex;align-items:center;content:""}.embla__dot--selected:after{box-shadow:inset 0 0 0 .2rem var(--text-body)}@media(max-width: 480px){.embla{flex-direction:column;align-items:flex-start;overflow:hidden}}.embla__caption{text-align:center;font-size:14px;color:#ccc;margin-top:8px}.embla__slide__number{filter:grayscale(100%);transition:filter .4s ease}.embla__slide__number:hover{filter:grayscale(0%)}