.Header{display:flex;justify-content:center;align-items:center;height:90px;width:100%;position:fixed;z-index:100;background-color:rgba(0,0,0,.65);backdrop-filter:blur(5px)}.Header>.content{width:1200px;display:flex;justify-content:space-between}.Header>.content>.burger{cursor:pointer}.Header>.content>.logo{cursor:pointer}.Header>.menu{width:100%;overflow-y:auto;-webkit-overflow-scrolling:touch;z-index:1000;width:100%;position:fixed;inset:0;background-color:#000;display:flex;justify-content:center;transition:all .3s}.Header>.menu.opened{transform:translateX(0);height:100vh}.Header>.menu.closed{transform:translateX(-100%)}.Header>.menu>.content{width:1200px;padding-top:34px;display:flex;flex-direction:column;gap:64px}.Header>.menu>.content>img.cross{width:22px;height:22px;cursor:pointer}.Header>.menu>.content>.links{display:flex;gap:64px}.Header>.menu>.content>.links>.linksCol{display:flex;flex-direction:column;min-width:300px;gap:16px}.Header>.menu>.content>.links>.linksCol>a.linkSmall{transition:all .2s;font-weight:500;font-size:16px;color:#fff}.Header>.menu>.content>.links>.linksCol>a.linkSmall:hover{opacity:.5}.Header>.menu>.content>.links>.linksCol>a.linkTitle{font-weight:400;font-size:24px;color:silver;font-family:"Russo One";transition:all .2s}.Header>.menu>.content>.links>.linksCol>a.linkTitle:hover{color:#fff}.Header>.menu>.content>.buttons{display:flex;flex-direction:column;align-items:flex-start;gap:10px}.Header>.menu>.content>.buttons>a{width:300px;height:45px;display:flex;justify-content:center;align-items:center}.Header>.menu>.content>.buttons>a.calcButton{border:1px solid #fff;background-color:#fff;color:#000;font-size:16px;font-weight:500}.Header>.menu>.content>.buttons>a.formButton{border:1px solid hsla(0,0%,100%,.6);background-color:rgba(0,0,0,0);color:hsla(0,0%,100%,.6);font-size:16px;font-weight:500}.Header>.menu>.content>img.logo{width:183px;height:25px}@media screen and (max-width: 1200px){.Header>.content{width:100%;padding:0px 24px}.Header>.menu>.content{width:100%;padding:34px 24px 0px 24px}}@media screen and (max-width: 664px){.Header>.menu>.content{gap:0px;justify-content:space-between}.Header>.menu>.content>img.logo{margin-bottom:24px}.Header>.menu>.content>.links{flex-direction:column;gap:24px}.Header>.menu>.content>.links>.linksCol{width:100%;gap:12px}.Header>.menu>.content>.links>.linksCol>a.linkTitle{font-size:20px !important}.Header>.menu>.content>.links>.linksCol>a.linkSmall{font-size:15px !important}.Header>.menu>.content>.buttons>a{width:100%}}@media screen and (max-width: 450px){.Header>.content>a{display:none}}